<template>
  <div class="home" :style="{paddingTop: page=='tv'?'0':'.82rem'}">
    <div class="head" v-show="page!='tv'">
      <div :style="'width:'+typeList.length*.78+'rem;'">
        <div
          class="head-item"
          v-for="(item,index) in typeList"
          @click.self="changeCategory(item,index)"
          :class="{active: type==index}">
          {{item.name}}
        </div>
      </div>
    </div>
    <template v-if="page=='index'">
      <matchList :category="category" :page="page"></matchList>
    </template>
    <template v-if="page=='tv'">
      <tv></tv>
    </template>
    <template v-if="page=='review'">
      <review :category="category"></review>
    </template>
    <div class="footer">
      <a href="javascript:void(0)" @click="changePage('index')">
        <img v-if="page=='index'" src="../../assets/images/footer/home_s.png" alt="">
        <img v-else src="../../assets/images/footer/home_n.png" alt="">
        <span>体育直播</span>
      </a>
      <a href="javascript:void(0)" @click="changePage('tv')">
        <img v-if="page=='tv'" src="../../assets/images/footer/tv_s.png" alt="">
        <img v-else src="../../assets/images/footer/tv_n.png" alt="">
        <span>电视直播</span>
      </a>
      <a href="javascript:void(0)" @click="changePage('review')">
        <img v-if="page=='review'" src="../../assets/images/footer/review_s.png" alt="">
        <img v-else src="../../assets/images/footer/review_n.png" alt="">
        <span>视频</span>
      </a>
    </div>
  </div>
</template>
<script>
  import matchList from '../components/matchList/matchList.vue'
  import tv from '../components/tv/tv.vue'
  import review from '../components/review/review.vue'
  export default {
    name: 'home',
    components: { matchList, tv, review },
    data () {
      return {
        typeList: [],
        type: 0,
        category: 'hot',
        page: 'index',
      }
    },
    created () {
      this.$nextTick(()=>{
        if (sessionStorage.getItem('page')) {
          this.page = sessionStorage.getItem('page')
        }
        this.getTypeList()
      })
    },
    methods: {
      changePage (type) {
        this.page = type
        sessionStorage.setItem('page',type)
        if (type=='index' || type == 'review') {
          this.category = this.typeList[0].id
          this.type = 0
        }
      },
      getTypeList () {
        this.$axios.get('game/category',{params: {date:this.getDateForm(),platform:'h5-m',version:2.0}})
          .then(res => {
            res = res.data
            this.typeList = res.data
            this.category = res.data[0].id
          })
      },
      changeCategory (item,idx) {
        this.type = idx
        this.category = item.id
      },
      getDateForm (time)  {
        if (!time) {
          time = new Date()
        }
        time = new Date(time)
        let y = time.getFullYear()
        let m = time.getMonth() + 1
        let d = time.getDate()
        m = m < 10 ? '0' + m : m
        d = d < 10? '0' + d : d

        return `${y}-${m}-${d}`
      }
    }
  }
</script>
<style>

  .home{
    width: 100%;
    height: 100%;
    position: relative;
    padding-top: .82rem;
    padding-bottom: 1rem;
    box-sizing: border-box;
  }
  .home .head{
    position: fixed;
    top:0;
    left: 0;
  }
  .head{
    width: 100%;
    height: 0.82rem;
    line-height: .82rem;
    background: #fff;
    overflow-x: scroll;
    font-size: .28rem;
    z-index: 999;
  }
  .head::-webkit-scrollbar {display:none}
  .head-content{
    width: 100%;
  }
  .head .head-item{
    margin: 0 .5rem;
    text-align: center;
    display: inline-block;
    color:#9B9B9B;
    background: #fff;
  }
  .head .head-item.active{
    color: #070A22;
    font-weight: bold;
  }
  .home .list{
    width: 100%;
    height: 100%;
    margin-top: .08rem;
  }

  .footer{
    width: 100%;
    position: fixed;
    z-index: 999;
    background:rgba(250,250,250,0.9);
    display: flex;
    bottom: 0;
    left: 0;
    font-size: .2rem;
    height: 1rem;
  }
  .footer a{
    display: block;
    flex: 1;
    text-align: center;
    cursor: pointer;
  }
  .footer a img{
    display: block;
    margin: 0 auto;
    width: .99rem;
  }
</style>



