<template>
  <div class="tv">
    <div class="tv-left">
      <div class="tv-type">
        <div
          v-for="(item,key) in tagList"
          :class="{active:tag==key}"
          @click="changeTag(key)"
          class="tv-type-item">
          {{item}}
        </div>
      </div>
    </div>
    <div class="tv-right" id="tv">
      <div class="tv-list">
        <a
          class="tv-item"
          v-for="(item,index) in list"
          :href="'player.html?matchId='+item.id+'&type=tv'">
          <img v-if="item.tvLogo" :src="item.tvLogo" alt="">
          <img v-else src="../../../assets/images/logoIcon.png" alt="">
          <span>{{item.tvName}}</span>
        </a>
        <div class="loading" v-show="loading">
          <img src="../../../assets/images/loading.gif" alt="">
          <span>正在努力加载中…</span>
        </div>
        <div v-if="nomore" class="nomore">没有更多数据了…</div>
      </div>
      <div v-show="loadBig" class="loadBig">
        <div class="load">
          <img src="../../../assets/images/loadBig.gif" alt="">
          <div>正在努力加载中…</div>
        </div>
      </div>
      <div v-show="nodata" class="nodata">
        <img src="../../../assets/images/nodata.png" alt="">
      </div>
    </div>
  </div>
</template>
<style lang="less">
  @import 'tv.less';
</style>
<script>
  export default{
    name: 'tv',
    data () {
      return {
        tagList: {},
        tag: '',
        list: [],
        pageNum: 1,
        pageSize: 15,
        loadBig: true,
        loading: false,
        nomore: false,
        nodata: false,
        scrollState: true
      }
    },
    created () {
      this.getTag()
      this.scrollMore()
    },
    methods: {
      getTag () {
        this.$axios.get('tvstation/tags').then(res => {
          res = res.data
          this.tagList = res.data
          let arr = []
          for (let i in this.tagList) {
            arr.push(i)
          }
          this.tag = arr[0]
          this.initData()
        })
      },
      changeTag (key) {
        this.tag = key
        this.list = []
        this.loadBig = true
        this.initData()
      },
      initData (pageNum = 1) {
        this.nodata = false
        this.nomore = false
        if (pageNum==1) {
          this.$nextTick(()=>{
            document.getElementById('tv').scrollTop = 0
          })
        } else {
          this.loading = true
        }
        this.$axios.get('tvstation/list',{params:{pageNum:pageNum,pageSize:this.pageSize,category:this.tag}})
          .then(res => {
            this.loadBig = false
            this.loading = false
            this.scrollState = true
            res = res.data
            if (res.code == 200) {
              let list = res.data
              if (pageNum==1) {
                this.list = list
                if ( this.list.length ==0) {
                  this.nodata = true
                } else if (list.length < this.pageSize) {
                  this.nomore = true
                }
              }else{
                this.list.push(...list)
                if (list.length < this.pageSize) {
                  this.nomore = true
                }
              }
            }
          })
      },
      scrollMore() {
        this.$nextTick(()=>{
          let box = document.getElementById('tv')
          box.addEventListener('scroll',()=>{
            let temp = box.scrollTop + box.offsetHeight

            if (temp + 100 >= box.scrollHeight) {
              if ((!this.nomore) && this.scrollState) {
                this.scrollState = false
                this.pageNum++
                this.initData(this.pageNum)
              }
            }
          })
        })
      },
    }
  }
</script>



