<template>
  <div class="matchList">
    <div class="list" :id="page">
      <a
        v-for="(item,index) in list"
        class="match-item"
        :href="'player.html?matchId='+item.gameId+'&type=index&title='+(item.homeName+'VS'+item.awayName)+'&status='+item.gameStatus">
        <div class="title">
          <span>{{item.leagueName}}</span>
          <span>{{getMatchTime(item.beginTime)}}</span>
        </div>
        <div class="team-info">
          <div class="team">
            <img v-if="item.homeLogo" :src="item.homeLogo" alt="">
            <img v-else src="../../../assets/images/logoIcon.png" alt="">
            <div>{{item.homeName}}</div>
          </div>
          <div class="status">
            <span>VS</span>
            <div class="status-name" :class="{red:item.gameStatus==2}">
              <span v-if="item.gameStatus==1">未开始</span>
              <span v-else-if="item.gameStatus==2">比赛中</span>
              <span v-else-if="item.gameStatus==3">已结束</span>
              <span v-else-if="item.gameStatus==4">即将开始</span>
            </div>
          </div>
          <div class="team">
            <img v-if="item.awayLogo" :src="item.awayLogo" alt="">
            <img v-else src="../../../assets/images/logoIcon.png" alt="">
            <div>{{item.awayName}}</div>
          </div>
        </div>
      </a>
      <div v-show="loadBig" class="loadBig">
        <div class="load">
          <img src="../../../assets/images/loadBig.gif" alt="">
          <div>正在努力加载中…</div>
        </div>
      </div>
      <div class="loading" v-show="showloading">
        <img src="../../../assets/images/loading.gif" alt="">
        <span>正在加载中…</span>
      </div>
      <div v-if="nodata" class="nodata">
        <img src="../../../assets/images/nodata.png" alt="">
      </div>
      <div v-if="nomore" class="nomore">没有更多数据了…</div>
    </div>
  </div>
</template>
<style lang="less">
  @import "matchList.less";
</style>
<script>
  export default{
    name: 'matchList',
    props: ['category','page'],
    data () {
      return {
        list: [],
        pageSize: 15,
        pageNum: 1,
        nodata: false,
        nomore: false,
        showloading: false,
        scrollStatus: true,
        loadBig: true,
      }
    },
    watch: {
      category: function (val,oldVal) {
        if (val != oldVal) {
          this.pageNum = 1
          this.nomore = false
          this.initData()
        }
      }
    },
    created () {
       this.initData()
    },
    mounted(){
      this.scrollMore()
    },
    methods: {
      initData (pageNum = 1) {
        this.nodata = false
        if (pageNum==1) {
          this.list = []
          this.loadBig = true
          this.$nextTick(()=>{
            document.getElementById(this.page).scrollTop = 0
          })
        } else {
          this.showloading = true
        }
        this.$axios.get('game/list',{
          params: {
            pageNum: pageNum,
            pageSize: this.pageSize,
            category: this.category
          }
        }).then(res => {
          this.showloading = false
          this.scrollState = true
          this.loadBig = false
          res = res.data
          if (res.code == 200) {
            let list = res.data
            if (pageNum==1) {
              this.list = list
              if ( this.list.length ==0) {
                this.nodata = true
              } else if (list.length < this.pageSize) {
                this.nomore = true
              }
            }else{
              this.list.push(...list)
              if (list.length < this.pageSize) {
                this.nomore = true
              }
            }
          } else {

          }
        })
      },
      scrollMore() {
        this.$nextTick(()=>{
          let box = document.getElementById(this.page)
          box.addEventListener('scroll',()=>{
            let temp = box.scrollTop + box.offsetHeight
            if (temp + 100 >= box.scrollHeight) {
              if ((!this.nomore) && this.scrollState) {
                this.scrollState = false
                this.pageNum++
                this.initData(this.pageNum)
              }
            }
          })
        })
      },
      getMatchTime (time) {
        if (time) {
          time = time.replace(/-/g,'/')
          time = new Date(time)
          let m = time.getMonth() + 1
          let d = time.getDate()
          let h = time.getHours()
          let mm = time.getMinutes()

          m = (m < 10 ? '0' + m : m)
          d = (d < 10 ? '0' + d : d)
          h = (h < 10 ? '0' + h : h)
          mm = (mm < 10 ? '0' + mm : mm)

          return `${m}-${d} ${h}:${mm}`
        } else {
          return time
        }
      }
    }
  }
</script>



