<template>
  <div class="review" id="review">
    <div class="review-list">
      <a
        class="review-item"
        v-for="(item,index) in list"
        :href="'player.html?matchId='+item.gameId+'&type=review'+'&category='+category + '&title='+(item.homeName+'VS'+item.awayName)">
        <img src="../../../assets/images/review.png" alt="">
        <div class="title">
          <div class="title-content"><span>{{item.homeName}}VS{{item.awayName}}</span></div>
          <span class="time">{{item.beginTime?item.beginTime.split(' ')[0]:item.beginTime}}</span>
        </div>
      </a>
      <div class="loading" v-show="loading">
        <img src="../../../assets/images/loading.gif" alt="">
        <span>正在努力加载中…</span>
      </div>
      <div v-if="nomore" class="nomore">没有更多数据了…</div>
    </div>
    <div v-show="loadBig" class="loadBig">
      <div class="load">
        <img src="../../../assets/images/loadBig.gif" alt="">
        <div>正在努力加载中…</div>
      </div>
    </div>
    <div v-show="nodata" class="nodata">
      <img src="../../../assets/images/nodata.png" alt="">
    </div>
  </div>
</template>
<style lang="less">
  @import 'review.less';
</style>
<script>
  export default {
    name: 'review',
    props: ['category'],
    data () {
      return {
        list: [],
        loading: false,
        nomore: false,
        loadBig: true,
        nodata: false,
        pageNum: 1,
        pageSize: 15,
        scrollState: true
      }
    },
    watch: {
      category: function (val, oldVal) {
        if (val != oldVal) {
          this.list = []
          this.pageNum = 1
          this.loadBig = true
          this.nodata = false
          this.nomore = false
          this.initData()
        }
      }
    },
    created (){
      this.initData()
      this.scrollMore()
    },
    methods: {
      initData (pageNum = 1) {
        this.nodata = false
        if (pageNum==1) {
          this.$nextTick(()=>{
            document.getElementById('review').scrollTop = 0
          })
        } else {
          this.loading = true
        }
        this.$axios.get('game/play-back',{params:{pageNum:pageNum,pageSize:this.pageSize,category:this.category}})
          .then(res => {
            this.loadBig = false
            this.loading = false
            this.scrollState = true
            res = res.data
            if (res.code == 200) {
              let list = res.data
              if (pageNum==1) {
                this.list = list
                if ( this.list.length ==0) {
                  this.nodata = true
                } else if (list.length < this.pageSize) {
                  this.nomore = true
                }
              }else{
                this.list.push(...list)
                if (list.length < this.pageSize) {
                  this.nomore = true
                }
              }
            }
          })
      },
      scrollMore() {
        this.$nextTick(()=>{
          let box = document.getElementById('review')
          box.addEventListener('scroll',()=>{
            let temp = box.scrollTop + box.offsetHeight
            if (temp + 100 >= box.scrollHeight) {
              if ((!this.nomore) && this.scrollState) {
                this.scrollState = false
                this.pageNum++
                this.initData(this.pageNum)
              }
            }
          })
        })
      },
    }
  }
</script>



